import { Wallet } from "src/helpers/types/Wallet";
import { TableData } from "src/components/Table/props";
import { fromUnixTimeStamp } from "src/helpers/time";
import { CopyToClipboard } from "react-copy-to-clipboard";
import { Referral } from "src/helpers/types/Referral";

export const formatWalletsForTable = (wallets: Wallet[], openSnackbar: (text: string) => void) => {
  const formatEntity = (entity: Wallet | Referral) => ({
    ...entity,
    reg_timestamp: fromUnixTimeStamp(entity.reg_timestamp),
    wallet: (
      <CopyToClipboard text={entity.wallet} onCopy={() => openSnackbar("Copied!")}>
        <span style={{ cursor: "pointer" }}>{entity.wallet_masked}</span>
      </CopyToClipboard>
    ),
  });

  const formattedWallets: TableData[] = wallets.slice().map((it) => {
    const row: TableData = {
      ...formatEntity(it),
      expandableData: it.referrals.map((rf) => {
        const referralRow: TableData = formatEntity(rf);
        return referralRow;
      }),
    };
    return row;
  });

  return formattedWallets;
};
