import React, { FC, ReactNode, useState } from "react";
import classNames from "classnames/dedupe";
import { cn } from "src/helpers/bem";
import { Props, TableData } from "./props";
import "./Table.scss";

const b = cn("site-table");

const EMPTY_COLUMNS_COUNT = 2;

export const Table: FC<Props> = (props) => {
  const { className, data, columns, withOrderColumn, hasMore, onLoadMore } = props;
  const [expandedRows, setExpandedRows] = useState<Record<number, number[]>>({});

  const removeFromExpandedRows = (index: number, deepLevel: number) =>
    setExpandedRows({ ...expandedRows, [deepLevel]: expandedRows[deepLevel].filter((it) => it !== index) });

  const addToExpandedRows = (index: number, deepLevel: number) => {
    if (!expandedRows[deepLevel]) {
      expandedRows[deepLevel] = [];
    }
    setExpandedRows({ ...expandedRows, [deepLevel]: [...expandedRows[deepLevel], index] });
  };

  const isRowExpandable = (row: TableData) => Array.isArray(row.expandableData) && row.expandableData.length > 0;

  const hasExpandableRows = data.some((it) => isRowExpandable(it));

  const renderTableRows = (tableData: TableData[], deepLevel: number): ReactNode => {
    return columns?.length ? (
      tableData.map((it, index) => {
        const thisRowExpanded = expandedRows[deepLevel]?.includes(index);
        return (
          <>
            <tr
              key={index}
              className={classNames(b("row", { expanded: thisRowExpanded, nested: deepLevel > 0 }), it.className)}
            >
              <td className={b("spacer-td")} />
              <td>{withOrderColumn && deepLevel === 0 ? index + 1 : null}</td>
              {columns.map((cl) => (
                <td key={cl.dataIndex}>{it[cl.dataIndex] !== undefined ? it[cl.dataIndex] : "—"}</td>
              ))}
              {isRowExpandable(it) ? (
                <td>
                  <button
                    className={b("btn", { expanded: thisRowExpanded })}
                    onClick={() => {
                      thisRowExpanded ? removeFromExpandedRows(index, deepLevel) : addToExpandedRows(index, deepLevel);
                    }}
                  >
                    {thisRowExpanded ? "Close" : "More"}
                  </button>
                </td>
              ) : null}
            </tr>
            {thisRowExpanded ? renderTableRows(it.expandableData, deepLevel + 1) : null}
          </>
        );
      })
    ) : (
      <tr className={b("empty-row")}>
        <td colSpan={columns.length} className={b("empty-text")}>
          No data
        </td>
      </tr>
    );
  };

  return (
    <table className={classNames(b(), className)}>
      <thead>
        <tr className={b("head-row")}>
          <th className={b("spacer-th")} />
          {withOrderColumn && <th />}

          {columns.map((it) => (
            <th key={it.dataIndex} className={b("th")}>
              {it.title}
            </th>
          ))}
          {hasExpandableRows ? <th /> : null}
        </tr>
      </thead>

      <tbody>
        {renderTableRows(data, 0)}

        {hasMore ? (
          <tr>
            <td
              className={b("load-more-wrapper")}
              colSpan={columns.length + Number(withOrderColumn) + EMPTY_COLUMNS_COUNT}
            >
              <button className={b("btn")} onClick={onLoadMore}>
                Load more
              </button>
            </td>
          </tr>
        ) : null}
      </tbody>
    </table>
  );
};
