import React, { FC, useEffect, useState } from "react";
import classNames from "classnames/dedupe";
import { cn } from "src/helpers/bem";
import { Props } from "./props";
import "./App.scss";
import { Table, Footer, Loader } from "src/components";
import { TableColumn } from "src/components/Table/props";
import { API } from "src/helpers/api";
import { GetWalletsResponse } from "src/helpers/types/network";
import { Wallet } from "src/helpers/types/Wallet";
import { formatWalletsForTable } from "src/helpers/wallets";
//@ts-ignore - для данного пакета нет typescript-схемы
import { useSnackbar } from "react-simple-snackbar";

const b = cn("App");

const columns: TableColumn[] = [
  { dataIndex: "wallet", title: "Wallet address" },
  { dataIndex: "reg_timestamp", title: "Registration Date" },
  { dataIndex: "referrals_count", title: "Referrals" },
  { dataIndex: "minted_ref", title: "Minted" },
  { dataIndex: "minted_own", title: "Own" },
];

export const App: FC<Props> = (props) => {
  const { className } = props;
  const [lastEvaluatedKey, setLastEvaluatedKey] = useState(undefined);
  const [wallets, setWallets] = useState<Wallet[]>([]);
  const [walletsLoading, setWalletsLoading] = useState(false);
  const [openSnackbar] = useSnackbar({ position: "top-center" });

  const fetchData = async () => {
    setWalletsLoading(true);
    try {
      const response: GetWalletsResponse = await API.get("", {
        params: { last_evaluated_key: lastEvaluatedKey },
      });
      setWallets([...wallets, ...response.results]);
      setLastEvaluatedKey(response.last_evaluated_key);
    } catch (e) {
      openSnackbar(`Failed to load data: ${e}`);
    }
    setWalletsLoading(false);
  };

  useEffect(() => {
    fetchData();
  }, []);

  return (
    <div className={classNames(b(), className)}>
      {walletsLoading && <Loader />}
      <div className={b("logo")} />
      <div className={b("inner")}>
        <h1 className={b("title")}>Referrals</h1>
        <div className={b("text")}>
          Panel for the administrator. It shows users who have referred others through referral links, as well as what
          purchases were made through those links.
        </div>
        <Table
          onLoadMore={fetchData}
          hasMore={Boolean(lastEvaluatedKey)}
          columns={columns}
          data={formatWalletsForTable(wallets, openSnackbar)}
          withOrderColumn
          className={b("table")}
        />
      </div>

      <Footer className={b("footer")} />
    </div>
  );
};
