import axios, { AxiosError, AxiosInstance, AxiosResponse } from "axios";

const baseURL =
  process.env.NODE_ENV === "production"
    ? process.env.REACT_APP_PRODUCTION_BASE_URL
    : process.env.REACT_APP_DEVELOPMENT_BASE_URL;

const setInterceptors = (api: AxiosInstance) => {
  api.interceptors.response.use(
    function (response: AxiosResponse) {
      if (response.data) {
        return response.data;
      }
    },
    function (error: AxiosError) {
      if (!error.response) {
        return Promise.reject("Check your internet connection");
      }

      if (error.message.includes("timeout")) {
        return Promise.reject("Request timeout");
      }

      const formattedError = JSON.stringify(
        //@ts-ignore
        error?.response?.data?.message || error?.response?.data?.error || error?.response?.data || "Unknown error"
      );
      return Promise.reject(formattedError);
    }
  );
};

let API = axios.create({
  baseURL,
  timeout: 20000,
  headers: {
    "Content-Type": "application/json",
  },
});
setInterceptors(API);

export { API };
